<template>
  <div>
    <!-- 폼 요소: 식당명 -->
    <div class="mb-3">
      <label for="sname" class="form-label">식당명</label>
      <input type="text" v-model="sname" class="form-control" id="sname" />
    </div>

    <!-- 폼 요소: 가게 분류 -->
    <div class="mb-3">
      <label for="fcategory_mikey" class="form-label">가게 분류</label>
      <select
        v-model="fcategory_mikey"
        class="form-select"
        id="fcategory_mikey"
      >
        <option value="301">한식</option>
        <option value="302">중식</option>
        <option value="303">일식</option>
        <option value="304">분식</option>
      </select>
    </div>

    <!-- 폼 요소: 주소 -->
    <div class="mb-3">
      <label for="saddr" class="form-label">주소</label>
      <input type="text" v-model="saddr" class="form-control" id="saddr" />
    </div>

    <!-- 폼 요소: 전화번호 -->
    <div class="mb-3">
      <label for="stel" class="form-label">전화번호</label>
      <input type="text" v-model="stel" class="form-control" id="stel" />
    </div>

    <!-- 폼 요소: 사업자번호 -->
    <div class="mb-3">
      <label for="bid" class="form-label">사업자번호</label>
      <input type="text" v-model="bid" class="form-control" id="bid" />
    </div>

    <!-- 폼 요소: 운영시간 -->
    <div class="mb-3">
      <label for="start_time" class="form-label">운영시간 시작</label>
      <input
        type="text"
        v-model="start_time"
        class="form-control"
        id="start_time"
      />
    </div>
    <div class="mb-3">
      <label for="end_time" class="form-label">운영시간 종료</label>
      <input
        type="text"
        v-model="end_time"
        class="form-control"
        id="end_time"
      />
    </div>

    <!-- 가게 사진 첨부 -->
    <div id="reviewImg" class="custom-file">
      <input id="customFile" type="file" ref="image" @change="upload" />
      <label class="custom-file-label" for="customFile">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="50"
          height="50"
          fill="currentColor"
          class="bi bi-card-image"
          viewBox="0 0 16 16"
        >
          <path d="M6.002 5.5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0" />
          <path
            d="M1.5 2A1.5 1.5 0 0 0 0 3.5v9A1.5 1.5 0 0 0 1.5 14h13a1.5 1.5 0 0 0 1.5-1.5v-9A1.5 1.5 0 0 0 14.5 2zm13 1a.5.5 0 0 1 .5.5v6l-3.775-1.947a.5.5 0 0 0-.577.093l-3.71 3.71-2.66-1.772a.5.5 0 0 0-.63.062L1.002 12v.54L1 12.5v-9a.5.5 0 0 1 .5-.5z"
          />
        </svg>
      </label>
    </div>

    <!-- 이미지 미리보기 -->
    <div id="imagePreview">
      <img
        v-if="imageUrl"
        :src="imageUrl"
        class="rounded mx-auto d-block"
        style="max-width: 100%; max-height: 300px"
      />
    </div>

    <!-- 등록 버튼 -->
    <button @click="submitRegistrationForm" class="btn btn-primary">
      가게 등록
    </button>
  </div>
</template>
  
<script>
import axios from "axios";

export default {
  name: "B_ShopReg",

  props: ["mnum"],
  data() {
    return {
      sname: "",
      fcategory_mikey: "301",
      saddr: "",
      stel: "",
      start_time: "",
      end_time: "",
      imageUrl: null,
      simg1: null,
      bid: "",
    };
  },
  mounted() {
    // 컴포넌트가 마운트되었을 때 mnum 값을 콘솔에 출력
    console.log("Received mnum:", this.mnum);
  },

  methods: {
    async upload() {
      // 이벤트에서 이미지를 가져옵니다.
      this.image = this.$refs.image.files[0];
      console.log(this.image);

      // 만약 이미지가 선택되지 않은 경우에는 처리를 종료
      if (!this.image) return;

      // FileReader 객체를 사용하여 이미지 파일을 데이터 URL로 읽어들입니다.
      const reader = new FileReader();
      reader.onload = (e) => {
        this.imageUrl = e.target.result; // 이미지 URL을 설정합니다.
      };
      reader.readAsDataURL(this.image); // 이미지 파일을 읽어들입니다.
    },

    async submitRegistrationForm() {
      // FormData 객체 생성
      try {
        const formData = {
          bnum: this.bnum,
          sname: this.sname,
          fcategory_mikey: this.fcategory_mikey,
          saddr: this.saddr,
          stel: this.stel,
          start_time: this.start_time,
          end_time: this.end_time,
          simg1: this.imageUrl, // 이미지를 Base64 문자열로 전달합니다.
          simg2: null,
          simg3: null,
          sconfirm: null,
          mnum: this.mnum,
          fcategory_makey: 300,
          sstatus_makey: 400,
          sstatus_mikey: 400,
          bstaus_makey: 500,
          bstaus_mikey: 500,
          bid: this.bid,
        };

        // 이미지가 존재하는 경우에만 이미지 URL 추가
        if (this.imageUrl) {
          formData.simg1 = this.imageUrl;
        }

        console.log("formData->" + formData); // 서버 응답 확인

        const response = await axios.post("/ShopReg", formData);

        // 서버로부터 응답을 받았을 때 실행할 작업
        console.log("가게 등록 완료:", response.data);
        // 성공적으로 작성된 경우 알람이 뜸
        alert("가게가 성공적으로 등록되었습니다.");
        // 성공알람 확인후 리스트로 이동
        // this.$router.push("/Home");
      } catch (error) {
        // 가게 등록 중 오류가 발생한 경우
        console.error("가게 등록 오류:", error);
        // 에러 메시지 표시
        alert("가게 등록에 실패했습니다.");
      }
    },
  },
};
</script>
