<template>
  <div>
    <!-- 사업자등록증 파일 업로드 -->
    <div id="bizRegFileImg" class="custom-file">
      <label for="bizRegFileImg">사업자등록증</label>
      <input id="customFile" type="file" ref="image" @change="upload" />
      <label class="custom-file-label" for="customFile">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="50"
          height="50"
          fill="currentColor"
          class="bi bi-card-image"
          viewBox="0 0 16 16"
        >
          <path d="M6.002 5.5a1.5 1.5 0 1 1-3 0 1.5 1.5 0 0 1 3 0" />
          <path
            d="M1.5 2A1.5 1.5 0 0 0 0 3.5v9A1.5 1.5 0 0 0 1.5 14h13a1.5 1.5 0 0 0 1.5-1.5v-9A1.5 1.5 0 0 0 14.5 2zm13 1a.5.5 0 0 1 .5.5v6l-3.775-1.947a.5.5 0 0 0-.577.093l-3.71 3.71-2.66-1.772a.5.5 0 0 0-.63.062L1.002 12v.54L1 12.5v-9a.5.5 0 0 1 .5-.5z"
          />
        </svg>
      </label>
    </div>

    <!-- 사업자등록증 이미지 미리보기 -->
    <div id="bizRegImagePreview">
      <img
        v-if="bizRegImageUrl"
        :src="bizRegImageUrl"
        class="rounded mx-auto d-block"
        style="max-width: 100%; max-height: 300px"
      />
    </div>

    <!-- 등록 버튼 -->
    <button @click="submitForm" class="btn btn-primary">업로드</button>
  </div>
</template>
    
  <script>
import axios from "axios";

export default {
  name: "B_FileInsert",
  props: ["mnum"],
  data() {
    return {
      bizRegImage: null,
      bizRegImageUrl: null,
    };
  },
  methods: {
    async upload() {
      // 이벤트에서 이미지를 가져옵니다.
      this.image = this.$refs.image.files[0];
      console.log(this.image);

      // 만약 이미지가 선택되지 않은 경우에는 처리를 종료
      if (!this.image) return;

      // FileReader 객체를 사용하여 이미지 파일을 데이터 URL로 읽어들입니다.
      const reader = new FileReader();
      reader.onload = (e) => {
        this.bizRegImageUrl = e.target.result; // 이미지 URL을 설정합니다.
      };
      reader.readAsDataURL(this.image); // 이미지 파일을 읽어들입니다.
    },

    async submitForm() {
      // FormData 객체 생성
      try {
        const formData = {
          mnum: this.mnum,
          bfnum: this.mnum,
          bftype_makey: 600,
          bftype_mikey: 601,
          bfimg1: this.bizRegImageUrl, // 이미지를 Base64 문자열로 전달합니다.
        };
        console.log("formData->" + formData); // 서버 응답 확인console.log("formData->" + formData); // 서버 응답 확인

        const response = await axios.post("/BFileInsert", formData);

        // 서버로부터 응답을 받았을 때 실행할 작업
        console.log("파일 등록 완료:", response.data);
        // 성공적으로 작성된 경우 알람이 뜸
        alert(
          "파일 등록 완료 되었습니다. 2~3일 정도 확인할 시간이 소요됩니다."
        );
        // 성공알람 확인후 리스트로 이동
        this.$router.push("/business/B_Ordering");
      } catch (error) {
        // 가게 등록 중 오류가 발생한 경우
        console.error("파일 등록 오류:", error);
        // 에러 메시지 표시
        alert("파일 등록에 실패했습니다.");
      }
    },
  },
};
</script>
  